<template>
  <div class="menu">
    <div @click.stop="show = !show" class="title">menu</div>
    <div class="tans" :class="show ? 'active' : 'noActive'">
      <div @click="exchange(index)" class="child" v-for="(item, index) of list" :key="index">{{item}}</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
      list: ["js", "vue"]
    };
  },
  methods: {
      exchange(e) {
          e ?  this.$router.push('life') : this.$router.push('/')
      }
  }
};
</script>

<style lang="less" scoped>
.menu {
  position: fixed;
  .rem(top, 90);
  .rem(width, 80);
  .rem(font-size, 15);
  text-align: center;
  transition: all 0.3s linear;
  overflow: hidden;
  height: 140px;
  .tans {
    transition: all 0.3s linear;
  }
  .title {
    .rem(height, 40);
  }
  .child {
    .rem(margin-top, 5);
    .rem(height, 40);
  }
  .active {
      .rem(height, 176) !important;
      overflow: hidden;
  }
  .noActive {
      .rem(height, 1) !important;
    overflow: hidden;
  }
}
</style>